          label: "分析对象",
          name:'analyzeObject'
        },
        {
          key: '3',
          label: "规则组",
          name:'ruleGroup'
        },
        {
          key: '4',
          label: "基本规则",
          name:'baseRule'
        },
      ],
      defaultKey: '1',
    };
  },
  methods: {
    tabClick(tab) {
      this.defaultKey = tab.key;
      this.$router.push({name:tab.name})
    },
  },
};
</script>
